.container{
  padding: 5px 10px;
  display: flex;
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: gray;
}
.pro{
  width: 100%;
  padding: 10px 10px;
  justify-content: space-between;
  flex-direction: column;
}
a{
width: 30%;
font-weight: bold;
flex-wrap: wrap;
justify-content: space-between;
margin: 20px;
position: left;
}